import Draw from './draw.vue'
import TweenMax from 'gsap'

let vueDraw = {}
vueDraw.install =  (Vue) => {
    //  - npm
    Vue.prototype.$TweenMax = TweenMax
    // 
    Vue.component(Draw.name, Draw)
}
//global
if (typeof  window !== 'undefined' && window.Vue) {
    window.Vue.use(vueDraw)
  }
export default vueDraw


